import * as React from "react";
import { Layout, Button } from "antd";
import { MergeTagsBuilderState } from "../../../models/mergeTagsBuilderState";


const { Header, Footer, Content } = Layout;

interface IProps {
}

export default class TemplateMergeTagsBuilder extends React.Component<IProps, MergeTagsBuilderState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            metadata: []
        };
    }

    async componentDidMount() {
        await this.test();
    }

    async test() {
        var dynamicsWebApi = new DynamicsWebApi();

        var request = {
            collection: 'EntityDefinitions',
            key: 'LogicalName="account"',
            select: ['LogicalName', 'SchemaName']
        };

        dynamicsWebApi.retrieveRequest(request).then(function (entityMetadata: any) {
            console.log(entityMetadata.Attributes);
        }).catch(function (error: any) {
            console.log(error);
        });
    }

    render() {
        return (
            <div>
                <Layout>
                    <Header>Header</Header>
                    <Content>
                        <div>
                            Content
                        </div>
                        <div>
                            <Button type="link" size={'small'}>
                                добавить</Button>
                        </div>
                    </Content>
                    <Footer>
                        <Button type="primary">Сохранить</Button>
                        <Button>Закрыть</Button>
                    </Footer>
                </Layout>
            </div>);
    }
}
